.transactionHistory {
  width: 600px;
  border-collapse: collapse;
}
.titleHead {
  background-color: #6ba1c5;
  color: aliceblue;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 18px;
  height: 60px;
}
.textBody {
  font-size: 16px;
  color: rgb(49, 47, 47);
}
.stringBody {
  height: 30px;
}
.stringBody:nth-child(2n) {
  background-color: rgb(210, 238, 245);
}
.tdBody {
  padding-left: 70px;
  border: 1px solid grey;
  text-transform: capitalize;
}
